import React from 'react'


export default function Alert(props) {
    const capitalize = (word) => {
        const capText = word.toLowerCase();
        return capText.charAt(0).toUpperCase() + capText.slice(1).toLowerCase();
    }

    return (
        // short form of if (this is true then this is to be done) statement
        <div style={{height:"50px", padding:"5px"}}>
        {props.alert && <div className={`alert alert-${props.alert.type} alert-dismissible fade show`} role="alert"><strong>{capitalize("alert")}</strong>: {capitalize(props.alert.msg)}</div>}
        </div>
    )
}
