import React from 'react'
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

export default function Navbar(props) {
    return (
        <nav className={`navbar navbar-expand-lg navbar-${props.mode} bg-${props.mode} sticky-top`}>
            <div className="container-fluid">
                <Link className="navbar-brand" to="/">{props.title}</Link>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                        <li className="nav-item">
                            <Link className="nav-link" aria-current="page" to="/">Home</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" aria-current="page" to="/about">{props.about}</Link>
                        </li>
                    </ul>

                    <div className="d-flex">
                        <div className="bg-primary rounded mx-2" style={{width:"25px", height:"25px",cursor:"pointer"}} onClick={()=>{props.colorBtn("primary")}}></div>
                        <div className="bg-success rounded mx-2" style={{width:"25px", height:"25px",cursor:"pointer"}} onClick={()=>{props.colorBtn("success")}}></div>
                        <div className="bg-danger rounded mx-2" style={{width:"25px", height:"25px",cursor:"pointer"}} onClick={()=>{props.colorBtn("danger")}}></div>
                        <div className="bg-warning rounded mx-2" style={{width:"25px", height:"25px",cursor:"pointer"}} onClick={()=>{props.colorBtn("warning")}}></div>
                    </div>

                    <div className={`form-check form-switch m-3 ms-0 text-${props.mode === 'light' ? 'dark' : 'light'}`}>
                        <input className="form-check-input" type="checkbox" id="flexSwitchCheckDefault" onClick={props.toggleBtn}/>
                        <label className="form-check-label" htmlFor="flexSwitchCheckDefault">Dark/Light</label>
                    </div>

                    <form className="d-flex" role="search">
                        <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search" />
                        <button className="btn btn-outline-success" type="submit">Search</button>
                    </form>
                </div>
            </div>
        </nav>
    )
}

// Validating the value of title and string as string 
Navbar.propTypes = {
    title: PropTypes.string.isRequired,
    about: PropTypes.string
};

// If props value is not passed, default value will be set here 
Navbar.defaultProps = {
    title: 'Set title here',
    about: 'Set about text here'
};