import React, { useState } from 'react'

export default function TextForm(props) {
    const [text, changeTextStyle] = useState("");
    // text = "new value"; // wrong way to change state value
    // changeTextStyle("new value"); // Correct way to change state value


    // Function to count words, characters and paragraph

    // const charWordPara = () => {
    //     let words = text.trim();
    //     if (words.length !== 0) {
    //         words = words.split(/\s+/);
    //     }

    //     let charac;
    //     let characLength;
    //     if (words.length === 0) {
    //         characLength = 0;
    //     }
    //     else {
    //         charac = String(words);
    //         characLength = charac.length - words.length + 1;
    //     }

    //     let para = text;
    //     let paraLength;
    //     if (words.length === 0) {
    //         paraLength = 0;
    //     }
    //     else {
    //         para = text.split(/\n+?\s+\w+/);
    //         paraLength = para.length;
    //     }

    //     const counting = document.getElementById("counting");
    //     counting.innerHTML = `${words.length} words and ${characLength} characters and ${paraLength} paragraphs`
    // }

    // To convert in uppercase 
    const changeToUppercase = () => {
        let textBox = document.getElementById("textBox");

        changeTextStyle(textBox.value.toUpperCase());
    }

    // To copy text to clipboard with alert
    const copyText = () => {
        navigator.clipboard.writeText(text);
        props.showAlert("your text is copied.", "success");
    }

    // to change the value of textarea or input value for textarea 
    const onChangeFn = (e) => {
        changeTextStyle(e.target.value);
    }

    // To clear all text
    const clearText = () => {
        changeTextStyle("");
    }

    // Remove extra spaces
    const removeExtraSpaces = () => {
        let newText = text.split(/\s+/);
        changeTextStyle((newText.join(" ")).trim());
    }

    // Making first letter capital of all words
    const firstLetterCapital = () => {
        let newText = text.split(/\s+/);
        let capitalFirst = "";
        newText.forEach(function (e) {
            capitalFirst += e.charAt(0).toUpperCase() + e.slice(1).toLowerCase() + " ";
        });
        capitalFirst = capitalFirst.trimEnd();
        changeTextStyle(capitalFirst);
    }

    return (
        <>
            <div className='container my-3' style={{ color: props.mode === 'light' ? 'black' : 'white' }}>
                <h1>{props.heading}</h1>
                <textarea className="form-control mt-3" value={text} placeholder="Type here ..." onChange={onChangeFn} id="textBox" rows="3" style={{ backgroundColor: props.mode === 'dark' ? 'gray' : 'white', color: props.mode === 'light' ? 'black' : 'white' }}></textarea>
                <button disabled={text.length === 0} className="btn btn-primary my-3" onClick={removeExtraSpaces}>Remove Extra Spaces</button>
                <button disabled={text.length === 0} className="btn btn-primary m-3" onClick={firstLetterCapital}>First Letter Capital</button>
                <button disabled={text.length === 0} className="btn btn-primary my-3" onClick={changeToUppercase}>Convert to Uppercase</button>
                <button disabled={text.length === 0} className="btn btn-primary m-3" onClick={copyText}>Copy Text</button>
                <button disabled={text.length === 0} className="btn btn-primary my-3" onClick={clearText}>Clear Text</button>

                <h2>Your text summary</h2>
                <p id='counting'>{text.trim().split(/\s+/).filter((element) => { return element.length !== 0 }).length} words, {text.length} characters and {text.trim().split(/\n+/).filter((element) => { return element.length !== 0 }).length} paragraphs</p>

                <h3>Preview Text:</h3>
                <p>{text.trim().length > 0 ? text : "Nothing to preview"}</p>
            </div>
        </>
    )
}
