import './App.css';
import About from './components/About';
import Navbar from './components/Navbar';
import TextForm from './components/TextForm';
import Alert from './components/Alert';
import React, { useState } from 'react';

import {
  BrowserRouter as Router,
  Routes,
  Route
} from "react-router-dom";

function App() {
  const [mode, setMode] = useState('light');
  const [alert, setAlert] = useState(null);

  const removeColor = () => {
    document.body.classList.remove("bg-primary");
    document.body.classList.remove("bg-danger");
    document.body.classList.remove("bg-success");
    document.body.classList.remove("bg-warning");
    document.body.classList.remove("bg-dark");
    document.body.classList.remove("bg-light");
  }
  
  const colorBtn = (cls) => {
    console.log("color btn")
    removeColor();
    document.body.classList.add("bg-" + cls);
    showAlert(cls + " mode is enabled.", cls);
  }

  const toggleBtn = () => {
    removeColor();

    if (mode === 'light') {
      setMode('dark');
      showAlert("dark mode is enabled.", "light");
      document.body.classList.add("bg-dark");
      document.body.classList.remove("bg-light");

    }
    else {
      setMode('light');
      // document.body.style.backgroundColor = 'white';
      document.body.classList.add("bg-light");
      document.body.classList.remove("bg-dark");
      showAlert("light mode is enabled.", "dark");
    }
  }

  const showAlert = (message, type) => {
    setAlert({
      msg: message,
      type: type
    })

    // Making alert auto removable after 2 seconds
    setTimeout(() => {
      setAlert(null);
    }, 2000);
  }

  return (
    <>
      <Router>
        <Navbar title="FirstApp" about="About" mode={mode} toggleBtn={toggleBtn} colorBtn={colorBtn} />
        <Alert alert={alert} />
        <Routes>
          {/* React do partial matching, so use exact path to match exact url  */}
          <Route exact path="/" element={
            <>
              <TextForm heading="Convert your text to UPPERCASE" mode={mode} showAlert={showAlert} />
            </>
          } />
        </Routes>
        <Routes>
          <>
            <Route path="/about" element={
              <About mode={mode} />} />
          </>
        </Routes>
      </Router >
    </>
  );
}

export default App;
